	module.exports = {"data":{"challengeNode":{"title":"Create Grids within Grids","description":["将元素转换为网格只会影响其子代元素。因此，在把某个子代元素设置为网格后，就会得到一个嵌套的网格。","例如，设置类为<code>item3</code>的元素的<code>display</code>和<code>grid-template-columns</code>属性，就会得到一个嵌套的网格。","<hr>","用<code>display</code>和<code>grid-template-columns</code>，使类为<code>item3</code>元素转换为有两列且宽度为<code>auto</code>和<code>1fr</code>的网格。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-grid/create-grids-within-grids","blockName":"CSS Grid","tests":[{"text":"<code>item3</code>类应该有<code>grid-template-columns</code>属性且值为<code>auto</code>和<code>1fr</code>。","testString":"assert(code.match(/.item3\\s*?{[\\s\\S]*grid-template-columns\\s*?:\\s*?auto\\s*?1fr\\s*?;[\\s\\S]*}/gi), '<code>item3</code>类应该有<code>grid-template-columns</code>属性且将<code>auto</code>和<code>1fr</code>作为值。');"},{"text":"<code>item3</code>类有<code>display</code>属性且值为<code>grid</code>。","testString":"assert(code.match(/.item3\\s*?{[\\s\\S]*display\\s*?:\\s*?grid\\s*?;[\\s\\S]*}/gi), '<code>item3</code>类有<code>display</code>属性且值为<code>grid</code>。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  .item1 {\n    background: LightSkyBlue;\n    grid-area: header;\n  }\n  \n  .item2 {\n    background: LightSalmon;\n    grid-area: advert;\n  }\n  \n  .item3 {\n    background: PaleTurquoise;\n    grid-area: content;\n    /* 请在本行以下添加你的代码 */\n    \n    \n    /* 请在本行以上添加你的代码 */\n  }\n  \n  .item4 {\n    background: lightpink;\n    grid-area: footer;\n  }\n  \n  .itemOne {\n    background: PaleGreen;\n  }\n  \n  .itemTwo {\n    background: BlanchedAlmond;\n  }\n  \n  .container {\n    font-size: 1.5em;\n    min-height: 300px;\n    width: 100%;\n    background: LightGray;\n    display: grid;\n    grid-template-columns: auto 1fr;\n    grid-template-rows: auto 1fr auto;\n    grid-gap: 10px;\n    grid-template-areas:\n      \"advert header\"\n      \"advert content\"\n      \"advert footer\";\n  }\n</style>\n  \n<div class=\"container\">\n  <div class=\"item1\">header</div>\n  <div class=\"item2\">advert</div>\n  <div class=\"item3\">\n    <div class=\"itemOne\">paragraph1</div>\n    <div class=\"itemTwo\">paragraph2</div>\n  </div>\n  <div class=\"item4\">footer</div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"/responsive-web-design/responsive-web-design-projects","template":null,"required":[],"nextChallengePath":"/responsive-web-design/responsive-web-design-projects/build-a-tribute-page","id":"5a94fe8569fb03452672e464"},"slug":"/responsive-web-design/css-grid/create-grids-within-grids"}}

